  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f8f8f8;
  border-radius: 8px;
  color: #666;
  font-size: 14px;
}

/* Override element-plus collapse styles for mobile */
:deep(.el-collapse) {
  border: none;
}

:deep(.el-collapse-item__header) {
  background-color: #fff;
  padding: 15px;
  border: none;
  font-size: 16px;
  height: auto;
  line-height: 1.4;
}

:deep(.el-collapse-item__content) {
  padding: 0;
  background-color: #fff;
}

:deep(.el-collapse-item__wrap) {
